.money-map {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--soft-mint) 0%, var(--soft-sky) 50%, var(--soft-lavender) 100%);
  background-image: url('https://images.unsplash.com/photo-1554224155-6726b3ff858f?w=1920&q=80');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-blend-mode: overlay;
  padding: 20px;
  position: relative;
}

.money-map::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.75);
  z-index: 0;
}

.header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  color: var(--text-dark);
  padding: 30px 40px;
  border-radius: 20px;
  margin-bottom: 30px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 1;
  text-align: center;
}

.header h1 {
  background: linear-gradient(135deg, var(--soft-indigo) 0%, var(--soft-violet) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 36px;
  font-weight: 700;
  margin: 0 0 10px 0;
}

.header .subtitle {
  color: var(--text-medium);
  font-size: 16px;
  margin: 0;
  font-weight: 400;
}

.back-btn {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  height: 40px;
  padding: 10px 20px;
  background: linear-gradient(135deg, var(--soft-indigo) 0%, var(--soft-violet) 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  box-shadow: 0 4px 15px rgba(212, 179, 255, 0.3);
}

.back-btn:hover {
  transform: translateY(-50%) translateX(-2px);
  box-shadow: 0 6px 20px rgba(212, 179, 255, 0.4);
}

.content {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.budget-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  align-items: start;
}

.input-panel {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 32px;
  border-radius: 20px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  border: 2px solid transparent;
}

.summary-panel {
  background: #ffffff;
  padding: 32px;
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 20px;
}

.summary-panel h2 {
  color: #1e88e5;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 25px;
  text-align: center;
  border-bottom: 3px solid #1e88e5;
  padding-bottom: 15px;
}

.section {
  margin-bottom: 30px;
}

.section h3 {
  color: #1e88e5;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 2px solid #e0e0e0;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #333333;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.3px;
}

.form-group input,
.form-group select {
  width: 100%;
  height: 48px;
  padding: 12px 16px;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  font-size: 15px;
  background: #ffffff;
  color: #333;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #1e88e5;
  box-shadow: 0 0 0 3px rgba(30, 136, 229, 0.2);
  background: #ffffff;
}

.calculate-btn {
  width: 100%;
  height: 48px;
  padding: 12px 24px;
  background: linear-gradient(135deg, #1e88e5 0%, #1565c0 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(30, 136, 229, 0.25);
  letter-spacing: 0.3px;
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.calculate-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(30, 136, 229, 0.35);
  background: linear-gradient(135deg, #1565c0 0%, #1e88e5 100%);
}

.summary-list {
  margin-bottom: 25px;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
  border-bottom: 1px solid #e0e0e0;
  font-size: 16px;
  color: #333333;
}

.summary-item:last-child {
  border-bottom: none;
}

.summary-item .amount {
  font-weight: 700;
  color: #1e88e5;
  font-size: 18px;
}

.total-section {
  background: linear-gradient(135deg, #2ecc71 0%, #27ae60 100%);
  padding: 25px;
  border-radius: 12px;
  text-align: center;
  margin-top: 20px;
  box-shadow: 0 4px 15px rgba(46, 204, 113, 0.3);
}

.total-label {
  color: rgba(255, 255, 255, 0.9);
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}

.total-amount {
  color: white;
  font-size: 36px;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.cost-breakdown {
  margin-top: 30px;
  padding-top: 25px;
  border-top: 2px solid #e0e0e0;
}

.cost-breakdown h3 {
  color: #1e88e5;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 15px;
}

.breakdown-item {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  border-bottom: 1px solid #f0f0f0;
  color: #666666;
  font-size: 15px;
}

.breakdown-item:last-child {
  border-bottom: none;
}

.save-btn {
  width: 100%;
  height: 48px;
  padding: 12px 24px;
  background: linear-gradient(135deg, #2ecc71 0%, #27ae60 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(46, 204, 113, 0.25);
  letter-spacing: 0.3px;
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(46, 204, 113, 0.35);
  background: linear-gradient(135deg, #27ae60 0%, #2ecc71 100%);
}

/* AI Section Styles */
.ai-section {
  margin-top: 25px;
  padding: 20px;
  background: linear-gradient(135deg, rgba(30, 136, 229, 0.1) 0%, rgba(21, 101, 192, 0.1) 100%);
  border-radius: 12px;
  border-left: 4px solid #1e88e5;
}

.ai-insights,
.ai-recommendations {
  margin-bottom: 20px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.ai-insights:last-child,
.ai-recommendations:last-child {
  margin-bottom: 0;
}

.ai-insights h4,
.ai-recommendations h4 {
  color: #1e88e5;
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 10px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.ai-insights p,
.ai-recommendations p {
  color: #555;
  font-size: 15px;
  line-height: 1.6;
  margin: 0;
}

.ai-badge {
  display: inline-block;
  padding: 8px 16px;
  background: linear-gradient(135deg, #1e88e5 0%, #1565c0 100%);
  color: white;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  margin-top: 15px;
  box-shadow: 0 2px 8px rgba(30, 136, 229, 0.3);
  text-align: center;
  width: 100%;
}

@media (max-width: 1024px) {
  .budget-container {
    grid-template-columns: 1fr;
  }
  
  .summary-panel {
    position: static;
  }
}

/* Calculation Mode Toggle */
.calculation-mode-section {
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid #e0e0e0;
}

.calculation-mode-section h3 {
  color: #1e88e5;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 15px;
}

.mode-toggle {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.mode-btn {
  flex: 1;
  min-width: 120px;
  padding: 12px 16px;
  background: #f5f5f5;
  color: #666;
  border: 2px solid #e0e0e0;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.mode-btn:hover {
  background: #e8f4f8;
  border-color: #1e88e5;
  color: #1e88e5;
}

.mode-btn.active {
  background: linear-gradient(135deg, #1e88e5 0%, #1565c0 100%);
  color: white;
  border-color: #1e88e5;
  box-shadow: 0 4px 12px rgba(30, 136, 229, 0.3);
}

/* Manual Budget Section */
.manual-budget-section {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 12px;
  margin-top: 20px;
  border: 2px dashed #1e88e5;
}

.manual-budget-section h3 {
  color: #1e88e5;
  font-size: 18px;
  margin-bottom: 15px;
}

/* Edit Button */
.edit-btn {
  width: 100%;
  height: 42px;
  padding: 10px 20px;
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(243, 156, 18, 0.25);
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.edit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(243, 156, 18, 0.35);
  background: linear-gradient(135deg, #e67e22 0%, #f39c12 100%);
}

/* Real-time Pricing Badge */
.realtime-badge {
  display: inline-block;
  padding: 8px 16px;
  background: linear-gradient(135deg, #2ecc71 0%, #27ae60 100%);
  color: white;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  margin-top: 15px;
  box-shadow: 0 2px 8px rgba(46, 204, 113, 0.3);
  text-align: center;
  width: 100%;
}

@media (max-width: 768px) {
  .header {
    padding: 20px;
  }
  
  .header h1 {
    font-size: 28px;
  }
  
  .back-btn {
    position: static;
    transform: none;
    margin-bottom: 15px;
  }
  
  .input-panel,
  .summary-panel {
    padding: 24px;
  }

  .mode-toggle {
    flex-direction: column;
  }

  .mode-btn {
    width: 100%;
  }
}
