.places-to-stay {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--seafoam-green) 0%, var(--ocean-blue) 100%);
  background-image: url('https://images.unsplash.com/photo-1566073771259-6a8506099945?w=1920&q=80');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-blend-mode: overlay;
  padding: 20px;
  position: relative;
}

.places-to-stay::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(178, 230, 206, 0.4);
  z-index: 0;
}

.header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 20px;
  border-radius: 15px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  position: relative;
  z-index: 1;
}

.header h1 {
  color: var(--ocean-blue);
  font-size: 32px;
  font-weight: 700;
  margin: 0;
}

.back-btn {
  height: 38px;
  padding: 8px 16px;
  background: linear-gradient(135deg, #1e88e5 0%, #1565c0 100%);
  color: white;
  border: none;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(30, 136, 229, 0.3);
  letter-spacing: 0.2px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  white-space: nowrap;
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(30, 136, 229, 0.4);
  background: linear-gradient(135deg, #1565c0 0%, #1e88e5 100%);
}

.content {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.section {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 35px;
  border-radius: 20px;
  margin-bottom: 30px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  border: 2px solid rgba(255, 255, 255, 0.5);
}

.section h2 {
  color: var(--ocean-blue);
  margin-bottom: 25px;
  font-size: 28px;
  font-weight: 700;
  text-align: center;
}

.items-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.item-card {
  background: linear-gradient(135deg, var(--seafoam-green) 0%, var(--seafoam-green-light) 100%);
  padding: 25px;
  border-radius: 15px;
  border: 2px solid transparent;
  transition: all 0.4s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.item-card:hover {
  transform: translateY(-5px) scale(1.03);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  border-color: var(--warm-coral);
  background: linear-gradient(135deg, var(--warm-coral) 0%, var(--sunny-yellow) 100%);
}

.item-card h3 {
  color: var(--earthy-brown);
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
}

.item-card:hover h3 {
  color: white;
}

.item-card p {
  color: var(--earthy-brown);
  margin: 8px 0;
  font-size: 14px;
}

.item-card:hover p {
  color: white;
}

/* Card Image */
.card-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
  margin: -25px -25px 15px -25px;
}

.card-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.view-details {
  margin-top: 10px;
  font-size: 12px;
  font-weight: 600;
  color: var(--ocean-blue);
  text-align: right;
}

.item-card:hover .view-details {
  color: white;
}

.description {
  font-size: 13px;
  line-height: 1.5;
  color: rgba(44, 62, 80, 0.7);
}

.item-card:hover .description {
  color: rgba(255, 255, 255, 0.9);
}

.cuisine-preview {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin-top: 5px;
}

.cuisine-tag-small {
  background: rgba(52, 152, 219, 0.2);
  color: var(--ocean-blue);
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 600;
}

.item-card:hover .cuisine-tag-small {
  background: rgba(255, 255, 255, 0.3);
  color: white;
}

/* Detail View */
.detail-view {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 35px;
  border-radius: 20px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  border: 2px solid rgba(255, 255, 255, 0.5);
}

.images-section {
  margin-bottom: 30px;
}

.images-section h3 {
  color: var(--ocean-blue);
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 700;
}

.images-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 15px;
}

.images-grid img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.images-grid img:hover {
  transform: scale(1.05);
}

.info-section {
  margin-bottom: 30px;
  padding-bottom: 25px;
  border-bottom: 2px solid rgba(52, 152, 219, 0.1);
}

.info-section:last-child {
  border-bottom: none;
}

.info-section h3 {
  color: var(--ocean-blue);
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: 700;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.info-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.info-item.full-width {
  grid-column: 1 / -1;
}

.info-label {
  font-weight: 700;
  color: var(--earthy-brown);
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.info-value {
  color: var(--ocean-blue);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.6;
}

/* Amenities & Cuisine */
.amenities-grid,
.cuisine-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.amenity-badge,
.cuisine-badge {
  background: linear-gradient(135deg, var(--seafoam-green) 0%, var(--ocean-blue) 100%);
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
}

/* Booking Link */
.booking-link-btn {
  display: inline-block;
  padding: 16px 32px;
  background: linear-gradient(135deg, #2ecc71 0%, #27ae60 100%);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(46, 204, 113, 0.3);
  text-align: center;
}

.booking-link-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(46, 204, 113, 0.4);
  background: linear-gradient(135deg, #27ae60 0%, #2ecc71 100%);
}

.loading,
.empty-message {
  text-align: center;
  padding: 40px;
  color: var(--earthy-brown);
  font-size: 18px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 15px;
}

/* Responsive */
@media (max-width: 768px) {
  .images-grid {
    grid-template-columns: 1fr;
  }

  .info-grid {
    grid-template-columns: 1fr;
  }

  .detail-view {
    padding: 20px;
  }
}
