@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap');

.stp-dashboard {
  min-height: 100vh;
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial;
  color: var(--lp-text, rgba(255, 255, 255, 0.92));
  background:
    linear-gradient(rgba(7, 15, 13, 0.72), rgba(7, 15, 13, 0.72)),
    url('https://www.thetravellingsloth.com/wp-content/uploads/2023/03/IMG_9389-1024x768.jpeg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  position: relative;
}

.stp-dashboard-overlay {
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 18% 10%, rgba(212, 163, 115, 0.16), transparent 44%),
    radial-gradient(circle at 82% 18%, rgba(26, 60, 52, 0.55), transparent 50%),
    radial-gradient(circle at 42% 88%, rgba(212, 163, 115, 0.14), transparent 48%);
  pointer-events: none;
}

.stp-nav {
  position: sticky;
  top: 0;
  z-index: 50;
  border-bottom: 1px solid rgba(255, 255, 255, 0.14);
  background: rgba(10, 20, 17, 0.35);
  backdrop-filter: blur(14px);
}

.stp-nav-inner {
  max-width: 1180px;
  margin: 0 auto;
  padding: 14px 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.stp-brand {
  appearance: none;
  border: 0;
  background: transparent;
  display: inline-flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  color: var(--lp-text, rgba(255, 255, 255, 0.92));
}

.stp-brand-logo {
  height: 44px;
  width: 44px;
  border-radius: 999px;
  object-fit: cover;
  object-position: center;
  border: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.28);
}

.stp-brand-text {
  font-weight: 900;
  letter-spacing: 0.2px;
  font-size: 14px;
}

.stp-logout {
  border: 1px solid rgba(255, 255, 255, 0.16);
  background: rgba(255, 255, 255, 0.08);
  color: rgba(255, 255, 255, 0.92);
  border-radius: 14px;
  height: 42px;
  padding: 0 14px;
  font-weight: 700;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  transition: transform 200ms ease, box-shadow 200ms ease, background-color 200ms ease, border-color 200ms ease;
}

.stp-logout:hover {
  transform: translateY(-2px);
  background: rgba(212, 163, 115, 0.14);
  border-color: rgba(212, 163, 115, 0.34);
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.26);
}

.stp-logout-icon {
  width: 18px;
  height: 18px;
}

.stp-shell {
  max-width: 1180px;
  margin: 0 auto;
  padding: 26px 18px 60px;
  position: relative;
  z-index: 1;
}

.stp-card {
  border-radius: 22px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.18);
  backdrop-filter: blur(12px);
  box-shadow: 0 22px 55px rgba(0, 0, 0, 0.28);
}

.stp-hero {
  padding: 24px;
  overflow: hidden;
}

.stp-hero-top {
  display: grid;
  grid-template-columns: 1fr minmax(280px, 420px);
  gap: 18px;
  align-items: start;
}

.stp-kicker {
  margin: 0;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  color: var(--lp-accent, #d4a373);
  font-weight: 800;
  letter-spacing: 0.18px;
  font-size: 13px;
}

.stp-kicker-icon {
  width: 16px;
  height: 16px;
}

.stp-hero-title {
  margin: 10px 0 6px;
  font-size: clamp(30px, 4vw, 44px);
  font-weight: 900;
  letter-spacing: -0.6px;
  line-height: 1.08;
  color: rgba(255, 255, 255, 0.96);
}

.stp-hero-subtitle {
  margin: 0;
  color: rgba(255, 255, 255, 0.74);
  font-weight: 500;
  line-height: 1.5;
  max-width: 56ch;
}

.stp-search {
  display: grid;
  grid-template-columns: 18px 1fr auto;
  align-items: center;
  gap: 10px;
  padding: 12px 12px;
  border-radius: 18px;
  border: 1px solid rgba(255, 255, 255, 0.16);
  background: rgba(7, 15, 13, 0.35);
}

.stp-search-icon {
  width: 18px;
  height: 18px;
  color: rgba(255, 255, 255, 0.78);
}

.stp-search-input {
  border: 0;
  outline: none;
  background: transparent;
  color: rgba(255, 255, 255, 0.92);
  font-weight: 600;
  font-size: 14px;
}

.stp-search-input::placeholder {
  color: rgba(255, 255, 255, 0.58);
  font-weight: 500;
}

.stp-search-btn {
  height: 38px;
  padding: 0 14px;
  border-radius: 14px;
  border: 0;
  cursor: pointer;
  font-weight: 800;
  letter-spacing: 0.2px;
  background: linear-gradient(135deg, var(--lp-accent, #d4a373), rgba(212, 163, 115, 0.86));
  color: #0c1a16;
  transition: transform 200ms ease, box-shadow 200ms ease, background-color 200ms ease;
}

.stp-search-btn:hover {
  transform: translateY(-2px);
  background: linear-gradient(135deg, #27ae60 0%, #2ecc71 100%);
  box-shadow: 0 18px 38px rgba(46, 204, 113, 0.2);
}

.stp-stats-row {
  margin-top: 18px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 14px;
}

.stp-stat {
  padding: 16px 18px;
  display: flex;
  align-items: center;
  gap: 14px;
}

.stp-stat-icon {
  width: 46px;
  height: 46px;
  border-radius: 16px;
  display: grid;
  place-items: center;
  background: rgba(212, 163, 115, 0.14);
  border: 1px solid rgba(212, 163, 115, 0.22);
  color: var(--lp-accent, #d4a373);
}

.stp-stat-icon svg {
  width: 22px;
  height: 22px;
}

.stp-stat-label {
  font-size: 12px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.66);
  letter-spacing: 0.2px;
}

.stp-stat-value {
  font-size: 22px;
  font-weight: 900;
  letter-spacing: -0.4px;
  color: rgba(255, 255, 255, 0.95);
  margin-top: 2px;
}

.stp-section {
  margin-top: 18px;
}

.stp-section-head {
  margin: 22px 0 14px;
}

.stp-section-title {
  margin: 0;
  font-size: 22px;
  font-weight: 900;
  letter-spacing: -0.3px;
  color: rgba(255, 255, 255, 0.94);
}

.stp-section-subtitle {
  margin: 6px 0 0;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  line-height: 1.5;
}

.stp-groups {
  display: grid;
  gap: 16px;
}

.stp-group {
  padding: 18px;
  border-radius: 22px;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(10px);
}

.stp-group-title {
  font-weight: 900;
  letter-spacing: 0.2px;
  color: var(--lp-accent, #d4a373);
  font-size: 13px;
  text-transform: uppercase;
  margin-bottom: 12px;
}

.stp-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 14px;
}

.stp-feature-card {
  width: 100%;
  text-align: left;
  padding: 16px;
  cursor: pointer;
  transition: transform 200ms ease, box-shadow 200ms ease, border-color 200ms ease, background-color 200ms ease;
}

.stp-feature-card:hover {
  box-shadow: 0 26px 62px rgba(0, 0, 0, 0.32);
  border-color: rgba(212, 163, 115, 0.34);
}

.stp-feature-icon {
  width: 46px;
  height: 46px;
  border-radius: 16px;
  display: grid;
  place-items: center;
  background: rgba(26, 60, 52, 0.45);
  border: 1px solid rgba(255, 255, 255, 0.14);
  color: rgba(255, 255, 255, 0.92);
}

.stp-feature-icon svg {
  width: 22px;
  height: 22px;
}

.stp-feature-body {
  display: grid;
  gap: 6px;
  margin-top: 12px;
}

.stp-feature-title {
  font-weight: 900;
  letter-spacing: -0.2px;
  color: rgba(255, 255, 255, 0.95);
  font-size: 16px;
}

.stp-feature-subtitle {
  color: rgba(255, 255, 255, 0.66);
  font-weight: 500;
  font-size: 13px;
  line-height: 1.35;
}

.stp-tone-gold {
  border-color: rgba(212, 163, 115, 0.5);
  background: rgba(212, 163, 115, 0.12);
}

.stp-tone-gold .stp-feature-icon {
  background: rgba(212, 163, 115, 0.22);
  border-color: rgba(212, 163, 115, 0.34);
  color: var(--lp-accent, #d4a373);
}

.stp-tone-gold:hover {
  border-color: rgba(46, 204, 113, 0.38);
  box-shadow: 0 26px 64px rgba(46, 204, 113, 0.14);
}

.stp-destinations {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 14px;
}

.stp-destination {
  appearance: none;
  border: 1px solid rgba(255, 255, 255, 0.18);
  background: rgba(255, 255, 255, 0.08);
  padding: 16px;
  cursor: pointer;
  text-align: left;
  display: grid;
  gap: 8px;
  transition: transform 200ms ease, box-shadow 200ms ease, border-color 200ms ease;
}

.stp-destination:hover {
  border-color: rgba(212, 163, 115, 0.34);
  box-shadow: 0 26px 62px rgba(0, 0, 0, 0.32);
}

.stp-destination-icon {
  width: 42px;
  height: 42px;
  border-radius: 16px;
  display: grid;
  place-items: center;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.14);
  color: var(--lp-accent, #d4a373);
}

.stp-destination-icon svg {
  width: 20px;
  height: 20px;
}

.stp-destination-name {
  font-weight: 900;
  color: rgba(255, 255, 255, 0.94);
  letter-spacing: -0.2px;
}

.stp-destination-meta {
  color: rgba(255, 255, 255, 0.66);
  font-weight: 500;
  font-size: 13px;
}

.stp-loading {
  padding: 18px;
  border-radius: 18px;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.14);
  color: rgba(255, 255, 255, 0.74);
  font-weight: 600;
}

@media (max-width: 980px) {
  .stp-hero-top {
    grid-template-columns: 1fr;
  }

  .stp-stats-row {
    grid-template-columns: 1fr;
  }

  .stp-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .stp-destinations {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 540px) {
  .stp-nav-inner {
    padding: 12px 14px;
  }

  .stp-brand-text {
    display: none;
  }

  .stp-hero {
    padding: 18px;
  }

  .stp-grid {
    grid-template-columns: 1fr;
  }

  .stp-destinations {
    grid-template-columns: 1fr;
  }
}
