/* Saved Items / Bucket List Page - Matching Design */

.saved-items-page {
  min-height: 100vh;
  background: #f5f5f5;
  padding: 0;
}

.saved-items-header {
  background: linear-gradient(135deg, #27ae60 0%, #2ecc71 100%);
  color: white;
  padding: 40px 30px;
  text-align: center;
}

.saved-items-header h1 {
  font-size: 42px;
  font-weight: 700;
  margin: 0 0 10px 0;
  color: white;
}

.saved-items-header .subtitle {
  font-size: 16px;
  margin: 0;
  opacity: 0.95;
  font-weight: 400;
}

.saved-items-tabs {
  display: flex;
  justify-content: center;
  gap: 0;
  background: white;
  border-bottom: 2px solid #e0e0e0;
  padding: 0 30px;
}

.saved-items-tabs .tab {
  padding: 18px 40px;
  background: none;
  border: none;
  border-bottom: 3px solid transparent;
  font-size: 16px;
  font-weight: 600;
  color: #666;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.saved-items-tabs .tab:hover {
  color: #27ae60;
  background: #f9f9f9;
}

.saved-items-tabs .tab.active {
  color: #27ae60;
  border-bottom-color: #27ae60;
  background: #f0fdf4;
}

.saved-items-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 40px 30px;
}

.saved-items-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 30px;
}

.saved-item-card {
  background: white;
  border-radius: 16px;
  padding: 25px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  position: relative;
  border: 1px solid #e8e8e8;
}

.saved-item-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.remove-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: none;
  background: #ff4444;
  color: white;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 10;
}

.remove-btn:hover {
  background: #cc0000;
  transform: scale(1.1);
}

.item-badge {
  position: absolute;
  top: 15px;
  left: 15px;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  z-index: 5;
}

.item-badge.popular {
  background: #27ae60;
  color: white;
}

.item-badge.adventure {
  background: #e74c3c;
  color: white;
}

.item-title {
  font-size: 24px;
  font-weight: 700;
  color: #27ae60;
  margin: 10px 0 8px 0;
  padding-right: 50px;
}

.item-location {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #666;
  font-size: 14px;
  margin-bottom: 12px;
}

.location-icon {
  font-size: 16px;
}

.item-description {
  color: #555;
  font-size: 14px;
  line-height: 1.6;
  margin: 0 0 15px 0;
  min-height: 44px;
}

.item-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 15px;
}

.item-tags .tag {
  background: #fff9e6;
  color: #8b6914;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
}

.item-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-top: 15px;
  border-top: 1px solid #f0f0f0;
}

.item-price {
  font-size: 18px;
  font-weight: 700;
  color: #2c3e50;
}

.item-rating {
  display: flex;
  align-items: center;
  gap: 4px;
}

.item-rating .star {
  font-size: 16px;
  color: #ddd;
}

.item-rating .star.filled {
  color: #ffc107;
}

.item-rating .rating-value {
  font-size: 14px;
  font-weight: 600;
  color: #666;
  margin-left: 4px;
}

.plan-trip-btn {
  width: 100%;
  padding: 12px 24px;
  background: #fff9e6;
  color: #8b6914;
  border: none;
  border-radius: 8px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.plan-trip-btn:hover {
  background: #ffeaa7;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 193, 7, 0.3);
}

/* Empty State */
.empty-state {
  background: white;
  border-radius: 16px;
  padding: 60px 40px;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

.empty-icon {
  font-size: 80px;
  margin-bottom: 20px;
}

.empty-state h2 {
  font-size: 28px;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 15px;
}

.empty-state p {
  color: #666;
  font-size: 16px;
  margin-bottom: 30px;
}

.explore-btn {
  padding: 14px 28px;
  background: linear-gradient(135deg, #27ae60 0%, #2ecc71 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(39, 174, 96, 0.3);
}

.explore-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(39, 174, 96, 0.4);
}

.loading {
  text-align: center;
  padding: 60px 40px;
  font-size: 18px;
  color: #666;
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

/* Responsive Design */
@media (max-width: 768px) {
  .saved-items-header {
    padding: 30px 20px;
  }

  .saved-items-header h1 {
    font-size: 32px;
  }

  .saved-items-tabs {
    padding: 0 10px;
    overflow-x: auto;
  }

  .saved-items-tabs .tab {
    padding: 15px 20px;
    font-size: 14px;
    white-space: nowrap;
  }

  .saved-items-content {
    padding: 20px 15px;
  }

  .saved-items-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .saved-item-card {
    padding: 20px;
  }
}
