.travel-fund {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--seafoam-green) 0%, var(--ocean-blue) 100%);
  background-image: url('https://images.unsplash.com/photo-1554224155-6726b3ff858f?w=1920&q=80');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-blend-mode: overlay;
  padding: 20px;
  position: relative;
}

.travel-fund::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(178, 230, 206, 0.4);
  z-index: 0;
}

.header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 20px;
  border-radius: 15px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  position: relative;
  z-index: 1;
}

.header h1 {
  color: var(--ocean-blue);
  font-size: 32px;
  font-weight: 700;
  margin: 0;
}

.back-btn {
  height: 38px;
  padding: 8px 16px;
  background: linear-gradient(135deg, #1e88e5 0%, #1565c0 100%);
  color: white;
  border: none;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(30, 136, 229, 0.3);
  letter-spacing: 0.2px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  white-space: nowrap;
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(30, 136, 229, 0.4);
  background: linear-gradient(135deg, #1565c0 0%, #1e88e5 100%);
}

.content {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.budgets-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 25px;
}

.budget-card {
  background: #ffffff;
  padding: 28px;
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.budget-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.12);
}

.budget-card h3 {
  color: var(--ocean-blue);
  margin-bottom: 15px;
  font-size: 24px;
  font-weight: 700;
}

.budget-card p {
  color: var(--earthy-brown);
  margin: 10px 0;
  font-size: 16px;
}

.total {
  font-size: 32px;
  font-weight: 700;
  color: var(--warm-coral);
  margin: 20px 0 !important;
  text-align: center;
  padding: 15px;
  background: linear-gradient(135deg, var(--sunny-yellow) 0%, var(--warm-coral) 100%);
  border-radius: 12px;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.delete-btn {
  width: 100%;
  height: 44px;
  padding: 10px 20px;
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(231, 76, 60, 0.25);
  letter-spacing: 0.3px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.delete-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(231, 76, 60, 0.35);
}

.empty, .loading {
  text-align: center;
  padding: 60px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  color: var(--earthy-brown);
  font-size: 20px;
  font-weight: 600;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}
